import React from "react";

//styles
import styles from './Note.module.scss';


const Note = () => {
  return (
    <div className={styles.Note}>
      <h3 className={styles.Note__heading}>
        
      </h3>
      <textarea
        name="note"
        className={styles.Note__field}
        placeholder=" ,     "
      />
    </div>
  );
};


export default Note;